<template>
  <header>
    <div class="header_logo">
      <router-link class="header_logo_img" to="/">
        <!-- <img src="">  -->
        Лого
      </router-link>
      <a class="header_logo_text"> Топовые фото-конкурсы </a>
    </div>
    <div class="header_main">
      <router-link to="/" :class="{header_main_active: $route.path == '/'}">Главная</router-link>
      <router-link to="/about" :class="{header_main_active: $route.path == '/about'}">О нас</router-link>
      <router-link to="/rules" :class="{header_main_active: $route.path == '/rules'}">Правила</router-link>
    </div>
    <div class="header_util">
      <a class="header_util_container header_search desktop">
        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon header_util_img svg-stroke-color'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M15 15l-3.757-3.757m0 0a6 6 0 10-8.485-8.485 6 6 0 008.485 8.485z" stroke="#F52F53" stroke-width="2"/></svg>
        <span class="header_util_style"> Поиск </span>
      </a>
      <div class="header_util_container header_util_style header_login_container">
        <template v-if="isLoggedIn">
          <router-link to="/account" class="header_login_wrapper desktop">
            <img src="@/static/images/profile-mini.png" />
            <div class="header_login">
              <span> Александр </span>
              <p>136 ₽</p>
            </div>
          </router-link>
          <div class="dropdown-icon_wrapper" @click="isDropdownOpen = !isDropdownOpen">
            <svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon dropdown-icon'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M9 1L5 5 1 1" stroke="#000" stroke-width="1.5"/></svg>
          </div>
          <div class="dropdown" v-if="isDropdownOpen" v-click-outside="() => {isDropdownOpen = false}">
            <ul>
              <li>
                <router-link to="/account"><span>Аккаунт</span></router-link>
              </li>
              <li @click="logout"><span>Выйти</span></li>
            </ul>
          </div>
        </template>
        <div class="header_util_style header_login-button desktop" v-else>
          <span @click="isLoginModalOpen = true">Войти</span>
        </div>

        <button class="hamburger hamburger--spin header_menu-button" @click="toggleMenu()" :class="{ 'is-active': isMenuActive }">
          <span class="hamburger-box">
            <span class="hamburger-inner"></span>
          </span>
        </button>
        <div class="header_menu_wrapper" :class="{active: isMenuActive}">
          <div class="full-bg" @click="onBgClick()" :class="{active: isMenuActive}"></div>
          <div class="header_menu_slider">
            <div class="header_menu">
              <ul class="header_menu_list">
                <li>
                  <a @click.prevent="goToLink('/')" href="/" :class="{header_main_active: $route.path == '/'}">Главная</a>
                </li>
                <li>
                  <a @click.prevent="goToLink('/about')" href="/about" :class="{header_main_active: $route.path == '/about'}">О нас</a>
                </li>
                <li>
                  <a @click.prevent="goToLink('/rules')" href="/rules" :class="{header_main_active: $route.path == '/rules'}">Правила</a>
                </li>
              </ul>
              <div class="header_menu_footer">
                <a class="header_util_container header_search">
                  <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon header_util_img svg-stroke-color'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M15 15l-3.757-3.757m0 0a6 6 0 10-8.485-8.485 6 6 0 008.485 8.485z" stroke="#F52F53" stroke-width="2"/></svg>
                  <span class="header_util_style"> Поиск </span>
                </a>
                <div class="header_menu_account-lang">
                  <router-link to="/account" class="header_login_wrapper" v-if="isLoggedIn">
                    <img src="@/static/images/profile-mini.png" />
                    <div class="header_login">
                      <span> Александр </span>
                      <p>136 ₽</p>
                    </div>
                  </router-link>
                  <div class="header_menu_log login" v-else>
                    <span @click="isLoginModalOpen = true">Войти</span>
                  </div>
                  <a class="header_util_container header_lang" @click="changeLang()">
                    <img
                      svg-inline
                      class="icon header_util_rus"
                      :src="require(`@/assets/icons/${lang}.svg`)"
                      alt="example"
                    />
                  </a>
                </div>
                <div class="header_menu_log" @click="logout" v-if="isLoggedIn"><span>Выйти</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <a class="header_util_container header_lang desktop" @click="changeLang()">
        <img
          svg-inline
          class="icon header_util_rus"
          :src="require(`@/assets/icons/${lang}.svg`)"
          alt="example"
        />

        <!-- <svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M9 1L5 5 1 1" stroke="#000" stroke-width="1.5"/></svg> -->
      </a>
    </div>

    <transition name="fade" mode="out-in">
      <div class="login-modal" v-if="isLoginModalOpen" :key="1" @click="isLoginModalOpen = false">
        <div class="login-modal__card" @click.prevent>
          <button class="login-modal__close-button" @click="isLoginModalOpen = false"></button>
          <h2 class="login-modal__title">Войти</h2>
          <div class="login-modal__social-buttons">
            <button>
              <svg width="21" height="12" viewBox="0 0 21 12" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon svg-path-color'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M19.687.833c.15-.47 0-.812-.662-.812h-2.2c-.555 0-.81.3-.96.62 0 0-1.132 2.733-2.712 4.505-.512.512-.747.683-1.025.683-.15 0-.341-.17-.341-.64V.81c0-.555-.171-.811-.641-.811h-3.46c-.34 0-.554.256-.554.512 0 .534.79.662.875 2.157v3.246c0 .704-.128.832-.406.832-.747 0-2.562-2.754-3.65-5.893C3.736.234 3.522 0 2.967 0H.748C.106 0 0 .299 0 .62c0 .576.747 3.48 3.48 7.323 1.815 2.626 4.399 4.036 6.726 4.036 1.41 0 1.58-.32 1.58-.854V9.139c0-.64.129-.748.577-.748.32 0 .897.171 2.2 1.431C16.056 11.317 16.312 12 17.145 12h2.2c.64 0 .939-.32.768-.94-.192-.619-.918-1.516-1.858-2.583-.512-.598-1.28-1.26-1.516-1.58-.32-.427-.235-.598 0-.982-.021 0 2.67-3.801 2.947-5.082z" fill="#F52F53"/></svg>
            </button>
            <button>
              <svg width="10" height="18" viewBox="0 0 10 18" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon svg-path-color'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M9.278.004L7.006 0C4.453 0 2.803 1.739 2.803 4.43v2.042H.52a.362.362 0 00-.358.367V9.8c0 .203.16.367.358.367h2.284v7.467c0 .203.16.367.358.367h2.98a.362.362 0 00.358-.367v-7.467H9.17a.362.362 0 00.357-.367l.001-2.96a.373.373 0 00-.104-.26.353.353 0 00-.253-.107H6.499V4.741c0-.832.193-1.255 1.248-1.255h1.53a.362.362 0 00.358-.367V.37a.362.362 0 00-.357-.367z" fill="#F52F53"/></svg>
            </button>
            <button>
              <svg width="10" height="18" viewBox="0 0 10 18" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon svg-path-color'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M9.278.004L7.006 0C4.453 0 2.803 1.739 2.803 4.43v2.042H.52a.362.362 0 00-.358.367V9.8c0 .203.16.367.358.367h2.284v7.467c0 .203.16.367.358.367h2.98a.362.362 0 00.358-.367v-7.467H9.17a.362.362 0 00.357-.367l.001-2.96a.373.373 0 00-.104-.26.353.353 0 00-.253-.107H6.499V4.741c0-.832.193-1.255 1.248-1.255h1.53a.362.362 0 00.358-.367V.37a.362.362 0 00-.357-.367z" fill="#F52F53"/></svg>
            </button>
          </div>
        </div>
      </div>
    </transition>
  </header>
</template>
<script>
export default {
  name: "Header",

  data () {
    return {
      lang: 'ru',
      isLoggedIn: true,
      isDropdownOpen: false,
      isMenuActive: false,
      isLoginModalOpen: false
    }
  },

  methods: {
    changeLang() {
      if (this.lang == 'ru') this.lang = 'en';
      else if (this.lang == 'en') this.lang = 'ru';
    },

    goToLink(link) {
      this.isMenuActive = false;
      this.$router.push(link);
    },

    logout() {
      this.isLoggedIn = false;
    },

    toggleMenu() {
      if(this.isMenuActive)
        this.closeMenu();
      else this.openMenu();
    },

    openMenu() {
      this.isMenuActive = true;
    },

    closeMenu() {
      this.isMenuActive = false;
    },

    onBgClick() {
      this.closeMenu();
    }
  }
};
</script>
